import React, { Component } from 'react';

import { MdDelete } from 'react-icons/md';

export default class AgGridActionColumnRenderer extends Component {

    constructor(props) {
        super(props);

        this.onDeleteClick = this.onDeleteClick.bind(this);
    }

    onDeleteClick() {

        console.log(this.props.node);
        this.props.context.componentParent.delete(
            this.props.node.data, this.props.node.rowIndex
        );
    }

    render() {
            if(this.props.value === "delete"){
                return (
                    <>
                        <MdDelete size='2em' onClick={this.onDeleteClick}/>
                    </>
                )
            }
    }
}