import React, { Component } from 'react';

import Badge from 'react-bootstrap/Badge';
import { MdCheck } from 'react-icons/md';
import { MdClose } from 'react-icons/md';

import ReactTooltip from 'react-tooltip';

export default class AgGridStatusBadgeRenderer extends Component {

  reasons = [
    "Specific disease is not covered under the scheme",
    "Plan is expired",
    "Hospital in not under approved list coverage",
    "Claim amount exceeds the limit",
    "Family member is not covered under the scheme"
  ];
  invokeParentMethod() {
    this.props.context.componentParent.methodFromParent(
      `Row: ${this.props.node.rowIndex}, Col: ${this.props.colDef.headerName}`
    );
  }

  render() {
    if(this.props.value === "Initial")return <Badge variant='primary'> Initial </Badge>;
    else if(this.props.value === "Pending Info")return <Badge variant='warning'> Pending Information </Badge>;
    else if(this.props.value === "Pending Approval")return <Badge variant='info'> Pending Approval </Badge>;
    else if(this.props.value === "Processing")return <Badge variant='success'> Processing </Badge>;
    else if(this.props.value === "Error")return <Badge variant='danger'> Error </Badge>;
    else if(this.props.value === "Accepted")return <Badge variant='success'> <MdCheck /> </Badge>;
    else if(this.props.value === "Rejected")return (
      <>
        <Badge variant='danger' data-tip={this.reasons[Math.floor(Math.random() * 5)]} data-place='right'> <MdClose /> </Badge>
        <ReactTooltip />
      </>
    );
  }
}