import React from 'react';

import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import NavDropdown from 'react-bootstrap/NavDropdown';

import { MdPerson } from 'react-icons/md';
import { MdNotifications } from 'react-icons/md';
import { MdExitToApp } from 'react-icons/md';

import "./KMRHeader.css";


class KMRHeader extends React.Component {

    render() {
        return (
            <Navbar className="customblue" expand="lg">
                <Navbar.Brand href="#home">KMR Systems</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">

                    <div className="header-right-section">
                        <Navbar className="right-nav">

                            <NavDropdown title={
                                <React.Fragment>
                                    <MdNotifications />
                                </React.Fragment>
                            } id="nav-dropdown" className="nav-dropdown">
                                <NavDropdown.Item eventKey="4.1"> Claim# 51322 is assigned <br /> to you for approval </NavDropdown.Item>
                                <NavDropdown.Divider />
                                <NavDropdown.Item eventKey="4.1"> More than expected number <br /> of claims getting in queue </NavDropdown.Item>
                                <NavDropdown.Divider />
                                <NavDropdown.Item eventKey="4.2"> Claim# 31253 is <br /> required address details. </NavDropdown.Item>
                            </NavDropdown>

                            <NavDropdown title={
                                <React.Fragment>
                                    <MdPerson />
                                </React.Fragment>
                            } id="nav-dropdown" className="nav-dropdown">
                                <NavDropdown.Item eventKey="4.1"> My Profile  </NavDropdown.Item>
                                <NavDropdown.Item eventKey="4.2"> Settings </NavDropdown.Item>
                                <NavDropdown.Item eventKey="4.3"> Choose Theme </NavDropdown.Item>
                            </NavDropdown>

                            <Nav.Item>
                                <Nav.Link eventKey="link-2">
                                    <MdExitToApp />
                                </Nav.Link>
                            </Nav.Item>
                        </Navbar>
                    </div>

                </Navbar.Collapse>
            </Navbar>
        );
    }
}

export default KMRHeader;