import { axios } from 'axios'

class APIService {

    //host = "http://localhost";
    host = "http://69.2.39.181";
    port = "8082";

    urlMap = {
        "memberSuggestUrl" : "cdlsr/suggestmember/{param1}",
        "providerSuggestUrl" : "cdlsr/suggestprovider/{param1}",
        "fetchDepedentByMember" : "cdlsr/dependentlist/member/{param1}",
        "claimAutoSave" : "cdlsr/autosave",
        "getClaimById" : "cdlsr/claim/{param1}",
        "getClaimByMember" : "cdlsr/claim/member/{param1}",
        "getClaimsByStatus" : "cdlsr/claims/status/{param1}",
        "getMasterItemDropdownList" : "cdlsr/getMasterItemDropdownList/{param1}",

        /* Master item data entry */
        "getMasterItems" : "mdlsr/masteritems",
        "getmasterItemFormFields" : "mdlsr/masteritemfields/{param1}",
        "getTableFields" : "mdlsr/masteritemtablefields/{param1}",
        "getmasterSpecificMasterItemList" : "mdlsr/masteritemlist/{param1}",
        "createMasterItem" : "mdlsr/{param1}",
        "createMasterChildItem" : "mdlsr/{param1}/masterid/{param2}",
        "editMasterItem" : "mdlsr/{param1}",
        "editMasterChildItem" : "mdlsr/{param1}/masterid/{param2}",
        "getMasterItemById" : "mdlsr/get/{param1}/{param2}",
        "getMasterChildItemById" : "mdlsr/getchilditem/{param1}/masterid/{param2}/childid/{param3}",
        "deleteMasterItemById" : "mdlsr/delete/{param1}/{param2}",
        "deleteChildItemById" : "mdlsr/deletechilditem/{param1}/{param2}/{param3}/{param4}"
    }

    getApiUrl(purpose, params = []) {

        try{
            var ep = this.urlMap[purpose];
            if(ep == undefined) return null;

            var urlParts = ep.split("/");
            var paramPointer = 0;
            
            for(var i=0; i< urlParts.length; i++) {
                if(urlParts[i].indexOf("{param") > -1) {
                    urlParts[i] = params[paramPointer++];
                }
            }
            //console.log(params);
            //console.log(urlParts);
            var constructedUrl = this.host + ":" + this.port + "/" + urlParts.join("/");
            //var constructedUrl = "/" + urlParts.join("/");
            return constructedUrl;

        } catch(e) {
            console.err(e);
            return null;
        }

    }

    invokeGet(url, obj, callback) {

        fetch(url).then(res => res.json()).then((response) => {
            if(response.status == "success")
                callback(response, obj);
            else alert("Error!!");
        })
    }

    invokePost(url, payload, obj, callback) {

        /*axios.post(url, payload)
        .then(response => {
            callback(response, obj);
        })*/
        fetch(
            url, 
            {
                method: "POST",
                body: payload,
                headers: {
                    'Content-Type': 'application/json'
                }
            }
        ).then(res => res.json()).then((response) => {
            callback(response, obj);
        })
    }

    invokePut(url, payload, obj, callback) {

        fetch(
            url, 
            {
                method: "PUT",
                body: payload,
                headers: {
                    'Content-Type': 'application/json'
                }
            }
        ).then(res => res.json()).then((response) => {
            callback(response, obj);
        })
    }

    invokeDelete(url, payload, obj, callback) {

        fetch(
            url, 
            {
                method: "DELETE",
                body: payload,
                headers: {
                    'Content-Type': 'application/json'
                }
            }
        ).then(res => res.json()).then((response) => {
            callback(response, obj);
        })
    }
}

export default APIService;