import React from 'react';

import {AgGridReact} from 'ag-grid-react';
import AgGridStatusBadgeRenderer from './AgGridStatusBadgeRenderer.jsx';

class KMRRejectedTabContent extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
          columnDefs: [
                { headerName: "Claim Id", field: "id" },
                { headerName: "Member Name", field: "name" },
                { headerName: "Member SSN", field: "ssn" },
                { headerName: "Last Update", field: "date" },
                { headerName: "Status", field: "status", cellRenderer: 'gridStatusBadgeRenderer' }
            ],
            rowData: [
                { id: "1001", name: "John Snow", ssn: 123456789, date: "12/12/2019", status: "Rejected" },
                { id: "1002", name: "Cerci Lanister", ssn: 987654321, date: "12/12/2019", status: "Rejected" },
                { id: "1003", name: "Rob Stark", ssn: 876543210, date: "12/12/2019", status: "Rejected" },
                { id: "1004", name: "Ned Stark", ssn: 7867642, date: "12/12/2019", status: "Rejected" },
                { id: "1005", name: "Tyrion Lanister", ssn: 312435, date: "12/12/2019", status: "Rejected" },
                { id: "1006", name: "Tom Clancy", ssn: 5463512, date: "12/12/2019", status: "Rejected" },
                { id: "1007", name: "Kevin Peterson", ssn: 67546134, date: "12/12/2019", status: "Rejected" },
                { id: "1008", name: "Michael Vaughn", ssn: 3456341, date: "12/12/2019", status: "Rejected" },
                { id: "1009", name: "Michel Stark", ssn: 6457458, date: "12/12/2019", status: "Rejected" },
                { id: "10010", name: "Steve Smith", ssn: 13243, date: "12/12/2019", status: "Rejected" },
                { id: "1011", name: "John McNally", ssn: 8562534, date: "12/12/2019", status: "Rejected" },
                { id: "1012", name: "Karl Hooper", ssn: 4523534, date: "12/12/2019", status: "Rejected" },
                { id: "1013", name: "Denis Lilly", ssn: 345342354, date: "12/12/2019", status: "Rejected" },
                { id: "1014", name: "Brian Smith", ssn: 234523, date: "12/12/2019", status: "Rejected" },
                { id: "1015", name: "Jack Ryan", ssn: 534254, date: "12/12/2019", status: "Rejected" }
            ],
            frameworkComponents: {
                gridStatusBadgeRenderer: AgGridStatusBadgeRenderer,
            }
        }
    }

    render() {
        return (
            <div id="grid-wrapper-rejected" style={{ width: '100%', height: '100%' }}>
                <div className="ag-theme-alpine ag-grid-container" style={ {height: '300px', width: '100%'} }>
                    <AgGridReact
                        columnDefs={this.state.columnDefs}
                        rowData={this.state.rowData}
                        frameworkComponents={this.state.frameworkComponents}
                        onGridSizeChanged={this.onGridSizeChanged.bind(this)}
                    >
                    </AgGridReact>
                </div>
            </div>
        );
    }

    

    onGridSizeChanged = params => {
        var gridWidth = document.getElementById('grid-wrapper-rejected').offsetWidth;
        var columnsToShow = [];
        var columnsToHide = [];
        var totalColsWidth = 0;
        var allColumns = params.columnApi.getAllColumns();
        for (var i = 0; i < allColumns.length; i++) {
          var column = allColumns[i];
          totalColsWidth += column.getMinWidth();
          if (totalColsWidth > gridWidth) {
            columnsToHide.push(column.colId);
          } else {
            columnsToShow.push(column.colId);
          }
        }
        params.columnApi.setColumnsVisible(columnsToShow, true);
        params.columnApi.setColumnsVisible(columnsToHide, false);
        params.api.sizeColumnsToFit();
    };
}

export default KMRRejectedTabContent;