import React from 'react';

import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Table from 'react-bootstrap/Table';
import Modal from 'react-bootstrap/Modal';

import moment from "moment";

import { AsyncTypeahead } from 'react-bootstrap-typeahead';
import Autosuggest from 'react-autosuggest';

import ApiService from './../../service/APIService';
import UtilityService from './../../service/UtilityService';

import "./style.css";
import 'bootstrap-daterangepicker/daterangepicker.css';

class CreateClaim extends React.Component {

    ctrlKeyPressed = false;
    altKeyPressed = false;

    isLoading= false;
    options = [];   

    apiService = new ApiService();

    inputProps = {
        placeholder: 'Member SSN',
        value: "",
        onChange: this.onChange
    };

    constructor(props) {

        super(props);

        this.types = ['MEDICAL', 'DENTAL']
        this.selectedTabIndex = 0;
        this.autoSaveInterval = undefined;
        this.lineItem = [
            {name: 'serviceFrom', type: 'date', value: '', label: 'Service From'},
            {name: 'serviceThru', type: 'date', value: '', label: 'Service Thru'},
            {name: 'place', type: 'text', value: '', label: 'Place'},
            {name: 'cpt', type: 'dropdown', value: '', options: [], masterdata: 'CPTMaster', label: 'CPT'},
            {name: 'procedure', type: 'dropdown', value: '', options: [], masterdata: 'ProcedureMaster', label: 'Procedure'},
            {name: 'modifier', type: 'text', value: '', label: 'Modifier'},
            {name: 'diagnosisPointer', type: 'text', value: '', label: 'Diagnosis Pointer'},
            {name: 'charges', type: 'number', value: '', label: 'Charges'},
            {name: 'allowed', type: 'number', value: '', label: 'Allowed'},
            {name: 'deductible', type: 'number', value: '', label: 'Deductible'},
            {name: 'notCovered', type: 'number', value: '', label: 'Not Covered'},
            {name: 'payment', type: 'number', value: '', label: 'Payment'},
            {name: 'exclusionCode', type: 'dropdown', value: '', options: [], masterdata: 'ExclusionCodeMaster', label: 'Exclusion Code'}
        ]

        /*
            NOTE: the attribute name starts with 
            1. member
            2. patient
            3. diagnosis
            4. other
             - needs to be used if are required to save
        */
        this.state = {
            key: 2,

            /* Claim ID */
            claimId: "",
            claimIdHeader: "",

            /* Member Auto Suggest */
            value: '',
            options: [],
            isLoading: false,

            /* Dependent Auto Suggest */
            depOptions: [],
            depValue: '',
            depIsLoading: false,

            /* Form Data */

            /* Master Details */
            masterClaimType: '',
            masterReceivedDate: '',

            /* Member */
            memberSSN: '',
            memberName: '',
            memberAddress: '',
            memberPolicy: '',
            memberCity: '',
            memberState: '',
            memberZip: '',
            memberTelephone: '',
            memberDob: '',
            memberSex: '',
            memberEmployer: '',
            memberInsurancePlan: '',

            /* Patient */
            patientRelation: '',
            patientSSN: '',
            patientName: '',
            patientDob: '',
            patientSex: '',
            patientAddress: '',
            patientCity: '',
            patientState: '',
            patientZip: '',
            patientTelePhone: '',
            patientMStat: '',
            patientEStat: '',
            patientCondition: '',

            /* Diagnosis */
            diagnosisCodes : [
                {pointer: 'A', value: ''}
            ],

            diagnosisLineItems : [
                JSON.parse(JSON.stringify(this.lineItem))
            ],

            /* Total */
            totalCharges: 0,
            totalAllowed: 0,
            totalDeductible: 0,
            totalNotCovered: 0,
            totalPayment: 0,

            /* Others */
            othersFedId: '',
            othersPatientAccountNum: '',
            othersPendCode: '',
            othersBillingNo: '',
            othersPayee: '',
            othersProvider: '',
            othersProviderAddress: '',

            /* Popup */
            showClaimPopup: false,
            historyClaims: []
        };

        this.handleSelect = this.handleSelect.bind(this);
        this.handlePatientChange = this.handlePatientChange.bind(this);
        this.handleDiagnosticCodeChange = this.handleDiagnosticCodeChange.bind(this);
        this.handleLineItemChange = this.handleLineItemChange.bind(this);
        this.commonChangeHandler = this.commonChangeHandler.bind(this);
        this.autoSave = this.autoSave.bind(this);

        this.tabNavKeyCheck();
    }

    componentDidMount() {
        var claimId = this.props.match.params.claimid;
        //means edit claim
        if(claimId != undefined){
            this.fetchClaim(claimId);
            this.setState({
                "claimId" : claimId,
                "claimIdHeader" : "[ Claim# : " + claimId + " ]"
            });
        }
        else {
            //means new claim
            this.setState({masterReceivedDate : moment().format("YYYY-MM-DD")})
        }

        this.populateMasterDropdown();
    }

    populateMasterDropdown() {

        //first fill up which are from user input
        for(var i=0; i< this.lineItem.length; i++) {
            if(this.lineItem[i].type == "dropdown") {
                if(this.lineItem[i].masterdata == "diagnosispointer") {

                }
            }
        }

        //Then fill up from master data items
        for(var i=0; i< this.lineItem.length; i++) {
            if(this.lineItem[i].type == "dropdown") {
                if(this.lineItem[i].masterdata != "diagnosispointer") {
                    this.invokeAPIToPopulateOptions(i, this.lineItem[i].masterdata);
                }
            }
        }
    }

    invokeAPIToPopulateOptions(index, masterData) {
        var root = this;
        var url = this.apiService.getApiUrl("getMasterItemDropdownList", [this.lineItem[index].masterdata]);
        this.apiService.invokeGet(url, this, function (response) {
            var data = response.data;

            root.lineItem[index].options = root.constructDropdownArray(data, masterData)
            root.setState({
                diagnosisLineItems : [JSON.parse(JSON.stringify(root.lineItem))]
            })
        })
    }

    constructDropdownArray(serverData, masterItem) {
        var list = [];
        for(var i = 0; i < serverData.length; i++) {
            var obj = {};
            if (masterItem == "CPTMaster") {
                obj.value = serverData[i]['CPT Id'];
                obj.display = serverData[i]['CPT Number'] + " ( " + serverData[i]['Description'] + ")";
            }
            else if(masterItem == "ProcedureMaster") {
                obj.value = serverData[i]['Procedure Id'];
                obj.display = serverData[i]['Procedure Number'] + " ( " + serverData[i]['Description'] + ")";
            } else if(masterItem == "ExclusionCodeMaster") {
                obj.value = serverData[i]['Exclusion Code Id'];
                obj.display = serverData[i]['Exclusion Code'] + " ( " + serverData[i]['Description'] + ")";
            }
            list.push(obj);
        }
        return list;
    }

    fetchClaim(claimId) {

        var url = this.apiService.getApiUrl("getClaimById", [claimId]);
        var root = this;
        this.apiService.invokeGet(url, this, function(response){
            var obj = response.data

            for (var key in obj) {
                if (obj.hasOwnProperty(key)) {
                    var val = obj[key];
                    if(key == 'diagnosisLineItems') {
                        
                        var finArr = [];
                        for(var i=0; i< val.length; i++) {
                            var itemObj = val[i];
                            var tempObj = {};
                            var tempArr = [];
                            for (var itemKey in itemObj) {
                                tempObj['name'] = itemKey; 
                                tempObj['type'] = "text"; 
                                tempObj['value'] = itemObj[itemKey]; 
                                tempArr.push(tempObj);
                            }
                            finArr.push(tempArr);
                        }

                        obj[key] = finArr;
                    }
                }
            }

            root.setState(obj);
            
        })
    }

    tabNavKeyCheck() {
        var root = this;
        document.onkeydown = function(e) {

            e = e || window.event;
            var key = e.key || e.keyIdentifier || e.keyCode;

            if(e.ctrlKey) { root.ctrlKeyPressed = true; }

            if(key == 'AltGraph') {
                //if(root.selectedTabIndex == 2) { //this if was required earlier because, then UI was tabbed based
                    root.state.diagnosisLineItems.push(JSON.parse(JSON.stringify(root.lineItem)));
               // }
            }

            if (e.keyCode == '37') {
                if(root.ctrlKeyPressed) {
                    if(root.selectedTabIndex > 0)root.selectedTabIndex--;
                }
            }
            else if (e.keyCode == '39') {
                if(root.ctrlKeyPressed) {
                    if(root.selectedTabIndex < 4)root.selectedTabIndex++;
                }
            }
            root.handleSelect(this.selectedTabIndex, false);
            this.ctrlKeyPressed = false;
        }
    }

    addNewLineItem() {
        var lineItems = this.state.diagnosisLineItems;
        var lineItemArr = JSON.parse(JSON.stringify(this.lineItem));

        lineItemArr[0].value = lineItems[lineItems.length - 1][0].value;
        lineItemArr[1].value = lineItems[lineItems.length - 1][1].value;

        lineItems.push(lineItemArr);
        this.setState({
            diagnosisLineItems: lineItems
        })
    }

    handleSelect(key, mouseClick = true) {
        if(mouseClick) {
            this.selectedTabIndex = key;
        }
        this.setState({key});
    }

    autoPopulateMember(selectedMember) {

        if(selectedMember == undefined) return;
        //console.log(selectedMember);
        var root = this;
        this.setState(
            {
                memberSSN:selectedMember.ssn,
                memberName:selectedMember.fname + ' ' + selectedMember.lname,
                memberAddress:selectedMember.address1 + ' ' + selectedMember.address2,
                memberPolicy:'',
                memberCity:selectedMember.city,
                memberState:selectedMember.state,
                memberZip:selectedMember.zip1,
                memberTelephone:selectedMember.phone,
                memberDob:UtilityService.formatDate(selectedMember.dob),
                memberSex:selectedMember.sex,
                memberEmployer:'',
                memberInsurancePlan :''
            }, function() {
                root.autoSave();
                root.fetchDependentList();
                document.getElementById("select-dependent").focus();
                //document.getElementById("select-dependent").scrollIntoView();
            }
        )

    }

    autoPopulateProvider(selectedProvider) {

        if(selectedProvider == undefined) return;

        var root = this;
        this.setState(
            {
                //temporary bad solution as provider data is not correct in db
                othersProvider          :   selectedProvider.docNumber.slice(-1) + selectedProvider.docDescription.trim(),
                othersProviderAddress   :   selectedProvider.address1.trim() + ", " + selectedProvider.city.trim() + ", " + selectedProvider.state.trim() + ", " + selectedProvider.zip.trim()
            }, function() {
                root.autoSave();
            }
        )

    }

    handlePatientChange(event) {

        var selectedValue = event.target.value;
        if(selectedValue == "")return;
        var root = this;
        if(selectedValue == 'Self') {

            this.setState(
                {
                    patientRelation: 'Self',
                    patientSSN : this.state.memberSSN,
                    patientName : this.state.memberName,
                    patientDob : this.state.memberDob,
                    patientSex : this.state.memberSex,
                    patientAddress : this.state.memberAddress,
                    patientCity : this.state.memberCity,
                    patientState : this.state.memberState,
                    patientZip : this.state.memberZip,
                    patientTelePhone : this.state.memberTelephone,
                    patientMStat : '',
                    patientEStat : '',
                    patientCondition : ''
                }, function() {
                        root.autoSave();
                }
            )
        } else {
            for(var i=0; i< this.state.depOptions.length; i++) {
                if(this.state.depOptions[i].dssn == selectedValue) {
                    this.autoPopulatePatient(this.state.depOptions[i]);
                    break;
                }
            }
        }
    }

    autoPopulatePatient(selectedDependent) {

        if(selectedDependent == undefined) return;

        if(selectedDependent.relation == 'W')selectedDependent.relation = 'Wife';
        else if(selectedDependent.relation == 'S')selectedDependent.relation = 'Son';
        else if(selectedDependent.relation == 'D')selectedDependent.relation = 'Daughter';

        var root = this;
        this.setState(
            {
                patientRelation: selectedDependent.relation,
                patientSSN: selectedDependent.dssn,
                patientName: selectedDependent.fname + ' ' + selectedDependent.lname,
                patientAddress: this.state.memberAddress,
                patientPolicy: '',
                patientCity: this.state.memberCity,
                patientState: this.state.memberState,
                patientZip: this.state.memberZip,
                patientTelephone: selectedDependent.phone,
                patientDob: UtilityService.formatDate(selectedDependent.dob),
                patientSex: selectedDependent.sex,
                patientEmployer: '',
                patientInsurancePlan: ''
            }, function() {
                root.autoSave();
            }
        )
    }

    handleDiagnosticCodeChange(event) {

        var root = this;
        var val = event.target.value;
        var diagnosis = this.state.diagnosisCodes;
        var index = event.target.dataset.index
        diagnosis[index].value = val;

        for(var i=0; i< diagnosis.length; i++) {
            if(diagnosis[i].value.trim() == "") {
                diagnosis.splice(i, 1);
            }
        }

        for(var i=0; i< diagnosis.length; i++) {
            if(i == 0)diagnosis[i].pointer = 'A';
            else diagnosis.pointer = UtilityService.nextLetter(diagnosis[i - 1].pointer);
        }

        var obj = {pointer: UtilityService.nextLetter(diagnosis[diagnosis.length - 1].pointer), value: ""}
        diagnosis.push(obj);

        this.setState({diagnosisCodes: diagnosis}, function(){
            root.autoSave();
        })
    }

    fetchDependentList() {
        var root = this;
        var depedentFetchUrl = this.apiService.getApiUrl("fetchDepedentByMember", [this.state.memberSSN]);
        this.apiService.invokeGet(depedentFetchUrl, this, function(response) {
            console.log(response);
            root.setState({depOptions : response.data});
        });
    }

    handleLineItemChange(event) {
        var index = event.target.dataset.index;
        var name = event.target.dataset.name;

        var lineItemsArr = JSON.parse(JSON.stringify(this.state.diagnosisLineItems));

        //make start date and end date same
        if(name == "serviceFrom") {
            //service thru is second item so index 1
            lineItemsArr[index][1].value = event.target.value;
        }

        var totalCharges = 0;
        var totalAllowed = 0
        var totalDeductible = 0;
        var totalNotCovered = 0;
        var totalPayment = 0;

        for(var i=0; i< lineItemsArr[index].length; i++) {
            if (lineItemsArr[index][i].name == name) {
                lineItemsArr[index][i].value = event.target.value;
            }
        }

        for(var j=0; j< lineItemsArr.length; j++) {
            for(var i=0; i< lineItemsArr[j].length; i++) {
                console.log(lineItemsArr[index][i].name);
                if (lineItemsArr[index][i].name == "charges") totalCharges += isNaN(parseFloat(lineItemsArr[j][i].value)) ? 0 : parseFloat(lineItemsArr[j][i].value);
                if (lineItemsArr[index][i].name == "allowed") totalAllowed += isNaN(parseFloat(lineItemsArr[j][i].value)) ? 0 : parseFloat(lineItemsArr[j][i].value);
                if (lineItemsArr[index][i].name == "deductible") totalDeductible += isNaN(parseFloat(lineItemsArr[j][i].value)) ? 0 : parseFloat(lineItemsArr[j][i].value);
                if (lineItemsArr[index][i].name == "notCovered") totalNotCovered += isNaN(parseFloat(lineItemsArr[j][i].value)) ? 0 : parseFloat(lineItemsArr[j][i].value);
                if (lineItemsArr[index][i].name == "payment") totalPayment += isNaN(parseFloat(lineItemsArr[j][i].value)) ? 0 :  parseFloat(lineItemsArr[j][i].value);
            }
        }
        console.log(lineItemsArr);

        var root = this;
        this.setState({
            diagnosisLineItems: lineItemsArr,
            totalCharges: totalCharges,
            totalAllowed: totalAllowed,
            totalDeductible: totalDeductible,
            totalNotCovered: totalNotCovered,
            totalPayment: totalPayment,
        }, function() {
            root.autoSave()
        })
    }

    commonChangeHandler(event) {

        var root = this;
        var name = event.target.dataset.name;
        this.setState({[name]: event.target.value}, function() {
            root.autoSave();
        });
    }

    autoSave(){
        
        document.getElementsByClassName("small-text")[0].classList.add("blink");
        if(this.autoSaveInterval != undefined)
            clearInterval(this.autoSaveInterval);
        
        var root = this;

        this.autoSaveInterval = setTimeout(function(){

            var autoSaveData = {};

            for (var key in root.state) {
                if (root.state.hasOwnProperty(key)) {

                    if(
                        key.startsWith("master") || key.startsWith("member") || key.startsWith("patient") || 
                        key.startsWith("diagnosis") || key.startsWith("other")) 
                    {
                            if(key == "diagnosisLineItems") {

                                var modifiedArr = [];
                                for(var i=0; i<root.state[key].length; i++) {

                                    var innerArr = root.state[key][i];
                                    var obj = {};
                                    for(var j=0; j< innerArr.length; j++) {
                                        obj[innerArr[j].name] = innerArr[j].value;
                                    }
                                    modifiedArr.push(obj);
                                }
                                autoSaveData[key] = modifiedArr;

                            } else {
                                autoSaveData[key] = root.state[key];
                            }
                    }
                }
            }

            if(root.state.claimId != undefined)
                autoSaveData['id'] = root.state.claimId;
            
            var autoSaveUrl = root.apiService.getApiUrl("claimAutoSave");
            root.apiService.invokePost(autoSaveUrl, JSON.stringify(autoSaveData), root, function(response) {
                //console.log(response);
                if(response.status === "success"){
                    root.setState({"claimId" : response.data.id});
                    root.setState({"claimIdHeader" : "[ Claim# : " + response.data.id + "]"});
                }
                document.getElementsByClassName("small-text")[0].classList.remove("blink");
            });

        }, 1500)
    }

    goToDashBoard() {
        window.location.href = "/portal/dashboard";
    }

    clearForm() {
        window.location.reload();
    }

    showClaimPopup() {

        if(this.state.memberSSN.length == 0) {
            alert("Member is not selected");
            return;
        }

        this.setState({showClaimPopup : true});

        var root = this;
        var claimHistoryUrl = this.apiService.getApiUrl("getClaimByMember", [this.state.memberSSN]);
        this.apiService.invokeGet(claimHistoryUrl, this, function(response) {
            root.setState({historyClaims : response.data});
        });
    }

    handleClaimPopupClose() {
        this.setState({showClaimPopup : false});
    }

    openHistoryClaim(event) {
        var claimId = event.target.dataset.claimid;
        window.open(
            window.location.href + "/" + claimId,
            "History Claim",
            "resizable,scrollbars,status"
        );
    }

    render() {

        return (
            <div className="create-claim-container">
                <Row className="headerRow">
                    <Col>
                        <span className="h3-header"> Claim Entry </span>
                        <span className="h3-header"> {this.state.claimIdHeader} </span>
                        <span className='small-text'> Saving... </span>
                    </Col>

                    <Col>
                        {/*<Button className="adjudicate-btn mateffect" variant="primary">
                            Adjudicate
                        </Button> */}

                        <Button className="clear-btn mateffect" variant="primary" onClick={this.clearForm.bind(this)}>
                            Clear
                        </Button>

                        <Button className="clear-btn mateffect" variant="primary" onClick={this.showClaimPopup.bind(this)}>
                            Claim History
                        </Button>

                        <Button className="claim-dashboard-btn mateffect" variant="primary" onClick={this.goToDashBoard.bind(this)}> 
                            Claim Dashboard
                        </Button>
                    </Col>
                </Row>

                <Form>
                    <Tabs activeKey={this.selectedTabIndex} id="create-claim-tabs" className="create-claim-tabs" onSelect={this.handleSelect}>
                        <Tab eventKey={0} title="Claim Information">
                            <Container fluid>
                                {/* Tab content start */}
                                <Form.Group as={Row} className="first-row" controlId="programTypes">
                                        <Form.Label column md="2" sm="2">
                                            Claim Type : 
                                        </Form.Label>
                                        
                                        <Col md="2" sm="2">
                                            <Form.Control as="select" value={this.state.masterClaimType} data-name="masterClaimType" onChange={this.commonChangeHandler}>
                                                { this.types.map((type) => (
                                                    <option value={type}> 
                                                        {type} 
                                                    </option>
                                                )) }
                                            </Form.Control>
                                        </Col>

                                        <Form.Label column md="2" sm="2">
                                            Received On : 
                                        </Form.Label>
                                        
                                        <Col md="2" sm="2">
                                            <Form.Control type="date" value={this.state.masterReceivedDate} data-name="masterReceivedDate" onChange={this.commonChangeHandler}/>
                                        </Col>

                                        <Form.Label column md="2" sm="2">
                                            Pend Code :
                                        </Form.Label>

                                        <Col md="2" sm="2">
                                            <Form.Control type="text" data-name="othersPendCode" value={this.state.othersPendCode} onChange={this.commonChangeHandler} placeholder="Enter Pend Code" />
                                        </Col>
                                </Form.Group>

                                <hr />

                                <Form.Row className="form-row">
                                    <Form.Group as={Col} controlId="insuredId">
                                        <Form.Label> Member's Name / SSN </Form.Label>
                                        <AsyncTypeahead
                                            id="member-auto-suggest"
                                            placeholder="Search Name / SSN"
                                            maxResults='6'
                                            autoFocus="true"
                                            isLoading={this.state.isLoading}
                                            labelKey={option => `${option.ssn} ${option.fname} ${option.lname}`}
                                            onSearch={(query) => {
                                                    
                                                this.setState({isLoading: true});
                                                fetch(this.apiService.getApiUrl("memberSuggestUrl", [query]))
                                                .then(resp => resp.json())
                                                .then(json => {
                                                    this.setState({
                                                        options: json.data
                                                    });
                                                    this.setState({isLoading: false});
                                                });
                                            }}

                                            onChange={(selected) => {

                                                this.autoPopulateMember(selected[0])
                                            }}

                                            renderMenuItemChildren={(option, props) => (
                                                <div className="suggestionContainer">
                                                    <span>{option.fname} {option.lname}</span>
                                                    <span className="smallSSNText"> (SSN : {option.ssn}) </span>
                                                </div>
                                            )}
                                            options={this.state.options}
                                        />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="memberName">
                                        <Form.Label>
                                            Member's Name 
                                        </Form.Label>
                                        <Form.Control type="text" value={this.state.memberName} placeholder="Enter Insured Name" />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="feca">
                                        <Form.Label> Member's Policy Group or FECA#</Form.Label>
                                        <Form.Control type="text" value={this.state.memberPolicy} placeholder="Enter Insured FECA#" />
                                    </Form.Group>
                                </Form.Row>

                                <Form.Row className="form-row odd-row">

                                    <Form.Group as={Col} controlId="memberAddress">
                                        <Form.Label>Member's Address</Form.Label>
                                        <Form.Control type="text" value={this.state.memberAddress} placeholder="Enter Insured Address" />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="memberCity">
                                        <Form.Label>City </Form.Label>
                                        <Form.Control type="text" value={this.state.memberCity} placeholder="City" />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="memberState">
                                        <Form.Label>
                                            Member's State
                                        </Form.Label>
                                        <Form.Control type="text" value={this.state.memberState} placeholder="State" />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="telephone" className="hidden">
                                        <Form.Label> Telephone# </Form.Label>
                                        <Form.Control type="text" value={this.state.memberTelephone} placeholder="(XXX) - XXX - XXXX" />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="zip">
                                        <Form.Label>Zip</Form.Label>
                                        <Form.Control type="text" value={this.state.memberZip} placeholder="Zip" />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="dob">
                                        <Form.Label>Date Of Birth </Form.Label>
                                        <Form.Control type="date" value={this.state.memberDob} placeholder="DOB" />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="sex">
                                        <Form.Label>
                                            Sex
                                        </Form.Label>
                                        <Form.Control as="select" value={this.state.memberSex} >
                                            <option value='M'> Male </option>
                                            <option value='F'> Female </option>
                                        </Form.Control>
                                        
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="empname" className="hidden">
                                        <Form.Label>Employer's Name</Form.Label>
                                        <Form.Control type="text" value={this.state.memberEmployer} placeholder="Employer's Name" />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="insurancePlanName" className="hidden">
                                        <Form.Label> Insurance Plan Name  </Form.Label>
                                        <Form.Control type="text" value={this.state.memberInsurancePlan} placeholder="Program Name" />
                                    </Form.Group>
                                </Form.Row>

                            </Container>

                            <hr />

                            <Container fluid>

                                <Form.Group as={Row} className="first-row" controlId="programTypes">

                                        <Form.Label column md="3" sm="3">
                                            Patient
                                        </Form.Label>

                                        <Col md="3" sm="3">
                                            <Form.Control
                                                as="select"
                                                value={this.state.patientSSN}
                                                onChange={this.handlePatientChange}
                                                id="select-dependent">
                                                <option value={""}>Select Dependent</option>
                                                <option value={"Self"}>Self</option>
                                                {
                                                    this.state.depOptions.map(function(field, index) {
                                                        return (
                                                            <option value={field.dssn}>
                                                                {field.fname.trim() + ' ' + field.lname.trim() + ' ' + field.initial.trim()}
                                                            </option>
                                                        )
                                                    })
                                                }
                                            </Form.Control>
                                        </Col>
                                </Form.Group>

                                <hr />
                                {/* ======================= Patient Details ======================*/}
                                <Form.Row className="first-row form-row">

                                    <Form.Group as={Col} controlId="relation">
                                        <Form.Label>
                                            Patient Relationship To Insured
                                        </Form.Label>
                                        <Form.Control type="text" value={this.state.patientRelation} placeholder="Patient Relation With Member" readOnly={true}/>
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="patient-name">
                                        <Form.Label>
                                            Patient's Name
                                        </Form.Label>
                                        <Form.Control type="text" value={this.state.patientName} placeholder="Enter Patient Name" readOnly={true}/>
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="patient-dob">
                                        <Form.Label> Patient's Date Of Birth</Form.Label>
                                        <Form.Control type="Date" value={this.state.patientDob} placeholder="Enter Patient DOB" readOnly={true}/>
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="pateint-sex">
                                        <Form.Label> Patient's Sex</Form.Label>
                                        <Form.Control as="select" value={this.state.patientSex} readOnly={true}>
                                            <option value=''>  </option>
                                            <option value='M'> Male </option>
                                            <option value='F'> Female </option>
                                        </Form.Control>
                                    </Form.Group>
                                </Form.Row>

                                {/* Hiding this row as per mail on Dec 8, 2020 */}
                                <Form.Row className="form-row odd-row hidden">

                                    <Form.Group as={Col} controlId="patientAddress">
                                        <Form.Label>Patient's Address (No., Street)</Form.Label>
                                        <Form.Control type="text" value={this.state.patientAddress} placeholder="Enter Patient Address" />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="patientCity">
                                        <Form.Label>City </Form.Label>
                                        <Form.Control type="text" value={this.state.patientCity} placeholder="City" />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="patientState">
                                        <Form.Label>
                                            State
                                        </Form.Label>
                                        <Form.Control type="text" value={this.state.patientState} placeholder="State" />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="patientZip">
                                        <Form.Label>Zip</Form.Label>
                                        <Form.Control type="text" value={this.state.patientZip} placeholder="Zip" />
                                    </Form.Group>
                                </Form.Row>

                                {/* Hiding this row as per mail on Dec 8, 2020 */}
                                <Form.Row className="form-row odd-row hidden">

                                    <Form.Group as={Col} controlId="patientTelephone">
                                        <Form.Label> Telephone# </Form.Label>
                                        <Form.Control type="text" value={this.state.patientTelePhone} placeholder="(XXX) - XXX - XXXX" />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="patientMstat">
                                        <Form.Label>
                                            Patient Marital Status
                                        </Form.Label>
                                        <Form.Control as="select" value={this.state.patientMStat}>
                                            <option> Single </option>
                                            <option> Married </option>
                                            <option> Others </option>
                                        </Form.Control>
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="patientEmpStat">
                                        <Form.Label>Patient Employment Status</Form.Label>
                                        <Form.Control as="select" value={this.state.patientEStat}>
                                            <option> Employed </option>
                                            <option> Full Time Student </option>
                                            <option> Part Time Student </option>
                                        </Form.Control>
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="patientCond">
                                        <Form.Label> Patient's Condition Related To </Form.Label>
                                        <Form.Control as="select" value={this.state.patientCondition}>
                                            <option> Employment </option>
                                            <option> Auto Accident </option>
                                            <option> Other Accident </option>
                                        </Form.Control>
                                    </Form.Group>
                                </Form.Row>
                            </Container>

                            <hr />
                            {/* =================== SERVICE DETAILS ====================== */}
                            <Container fluid>
                                <Form.Row className="first-row form-row">
                                    <Form.Group as={Col} controlId="diagnosis">
                                        <Form.Label>
                                            Diagnosis Or Nature Of Illness Or Injury
                                        </Form.Label>

                                        <Row>
                                            { this.state.diagnosisCodes.map((d, index) => (
                                                <Col md="2" sm="2">
                                                    <b> {d.pointer}. &nbsp; </b>
                                                    <Form.Control
                                                        className="uppercase-input"
                                                        type="text"
                                                        data-index={index}
                                                        onChange={this.handleDiagnosticCodeChange}
                                                        value={d.value}
                                                        placeholder="" />
                                                </Col>
                                            ))
                                            }
                                        </Row>
                                    </Form.Group>
                                </Form.Row>
                                <Form.Row className="first-row form-row">
                                    <Form.Group as={Col} controlId="lineItems">
                                        <Form.Label>
                                            Line Items
                                            <span style={{fontSize:10 + "px"}}>
                                                &nbsp; (Right Alt To Add More)
                                            </span>

                                            <Button className="addnewlineitem-btn mateffect" variant="primary" onClick={this.addNewLineItem.bind(this)}>
                                                Add New Line Item
                                            </Button>
                                        </Form.Label>
                                        <Row>
                                            <hr />
                                        </Row>
                                        <Row>
                                            <Table striped bordered hover size="sm" className="small-header-size-table">
                                                <tbody>

                                                {
                                                    this.state.diagnosisLineItems.map((value, index) => {
                                                        return (
                                                            <tr>
                                                                <td>
                                                                    <div className="diagnosis-line-item-container">
                                                                        {
                                                                            this.state.diagnosisLineItems[index].map((tVal, tIndex) => {
                                                                                if(tVal.type === "dropdown")
                                                                                    return (
                                                                                        <div>
                                                                                            <label> <b> {tVal.label} : </b> </label>
                                                                                            <Form.Control as="select"
                                                                                                          data-name={tVal.name}
                                                                                                          value={tVal.value}
                                                                                                          data-index={index}
                                                                                                          onChange={this.handleLineItemChange}>
                                                                                                <option value={''}></option>
                                                                                                {
                                                                                                    this.state.diagnosisLineItems[index][tIndex].options.map((tVal, tIndex) => {
                                                                                                        return (<option value={tVal.value}> {tVal.display} </option>)
                                                                                                    })
                                                                                                }
                                                                                            </Form.Control>
                                                                                        </div>
                                                                                    )
                                                                                else
                                                                                    return (
                                                                                        <div>
                                                                                            <label> <b> {tVal.label} : </b> </label>
                                                                                            <Form.Control
                                                                                                onChange={this.handleLineItemChange}
                                                                                                data-name={tVal.name}
                                                                                                data-index={index}
                                                                                                value={tVal.value}
                                                                                                type={tVal.type}
                                                                                            />
                                                                                        </div>
                                                                                    )
                                                                            })
                                                                        }
                                                                    </div>
                                                                    <hr />
                                                                </td>
                                                            </tr>
                                                        )
                                                    })
                                                }
                                                </tbody>
                                                <tr>
                                                    <td>
                                                        <div  class={"total-container"}>
                                                            <div>
                                                                <label> <b> Total Charges : </b> </label>
                                                                <Form.Control
                                                                    type="text"
                                                                    value={this.state.totalCharges}
                                                                    readonly
                                                                />
                                                            </div>

                                                            <div>
                                                                <label> <b> Total Allowed : </b> </label>
                                                                <Form.Control
                                                                    type="text"
                                                                    value={this.state.totalAllowed}
                                                                    readonly
                                                                />
                                                            </div>

                                                            <div>
                                                                <label> <b> Total Deductible : </b> </label>
                                                                <Form.Control
                                                                    type="text"
                                                                    value={this.state.totalDeductible}
                                                                    readonly
                                                                />
                                                            </div>

                                                            <div>
                                                                <label> <b> Total Not Covered : </b> </label>
                                                                <Form.Control
                                                                    type="text"
                                                                    value={this.state.totalNotCovered}
                                                                    readonly
                                                                />
                                                            </div>

                                                            <div>
                                                                <label> <b> Total Payment : </b> </label>
                                                                <Form.Control
                                                                    type="text"
                                                                    value={this.state.totalPayment}
                                                                    readonly
                                                                />
                                                            </div>
                                                        </div>
                                                    </td>
                                                </tr>
                                            </Table>
                                        </Row>
                                    </Form.Group>
                                </Form.Row>
                            </Container>

                            <hr />
                            {/* ==================  OTHERS TAB =================== */}
                            <Container fluid>
                                <Form.Row className="form-row">
                                    <Form.Group as={Col} controlId="fedtax">
                                        <Form.Label>Federal Tax Id# </Form.Label>
                                        {/*<Form.Control type="text" data-name="othersFedId" value={this.state.othersFedId} onChange={this.commonChangeHandler} placeholder="Enter Provider #" />*/}
                                        <AsyncTypeahead
                                            data-name="othersFedId"
                                            value={this.state.othersFedId}
                                            placeholder="Enter Provider #"
                                            id="fed-id"
                                            maxResults='6'
                                            isLoading={this.state.isLoading}
                                            labelKey={option => `${option.docNumber.slice(0,-1)}`}
                                            onSearch={(query) => {

                                                this.setState({isLoading: true});
                                                fetch(this.apiService.getApiUrl("providerSuggestUrl", [query]))
                                                    .then(resp => resp.json())
                                                    .then(json => {
                                                        this.setState({
                                                            options: json.data
                                                        });
                                                        this.setState({isLoading: false});
                                                    });
                                            }}

                                            onChange={(selected) => {

                                                this.autoPopulateProvider(selected[0])
                                            }}

                                            renderMenuItemChildren={(option, props) => (
                                                <div className="suggestionContainer">
                                                    <span>{option.docNumber.slice(0,-1)}</span>
                                                    <span className="smallSSNText"> ({option.docNumber.slice(-1) + option.docDescription}) </span>
                                                </div>
                                            )}
                                            options={this.state.options}
                                        />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="provider">
                                        <Form.Label> Provider </Form.Label>
                                        <Form.Control type="text" data-name="othersProvider" value={this.state.othersProvider} onChange={this.commonChangeHandler} placeholder="Enter Provider" />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="providerAddress">
                                        <Form.Label> Provider Address </Form.Label>
                                        <Form.Control type="text" data-name="othersProviderAddress" value={this.state.othersProviderAddress} onChange={this.commonChangeHandler} placeholder="Enter Provider Address" />
                                    </Form.Group>
                                </Form.Row>

                                <Form.Row className="form-row">

                                    <Form.Group as={Col} controlId="patientAcc">
                                        <Form.Label>
                                            Patient Account#
                                        </Form.Label>
                                        <Form.Control type="text" data-name="othersPatientAccountNum" value={this.state.othersPatientAccountNum} onChange={this.commonChangeHandler} placeholder="Patient Account Number" />
                                    </Form.Group>

                                    {/*<Form.Group as={Col} controlId="billing">
                                        <Form.Label> Billing# </Form.Label>
                                        <Form.Control type="text" data-name="othersBillingNo" value={this.state.othersBillingNo} onChange={this.commonChangeHandler} placeholder="Enter Billing#" />
                                    </Form.Group> */}

                                    <Form.Group as={Col} controlId="payee">
                                        <Form.Label> Payee </Form.Label>
                                        <Form.Control as="select" data-name="othersPayee" value={this.state.othersPayee} onChange={this.commonChangeHandler} placeholder="Enter Payee">
                                            <option> M - Member </option>
                                            <option> P - Provider </option>
                                            <option> D - Dependent </option>
                                        </Form.Control>
                                    </Form.Group>

                                </Form.Row>
                            </Container>
                        </Tab>
                    </Tabs>
                </Form>

                <Row className="headerRow">
                    <Col>
                        <Button className="adjudicate-btn mateffect" variant="primary">
                            Adjudicate
                        </Button>
                    </Col>
                </Row>

                <Modal show={this.state.showClaimPopup} onHide={this.handleClaimPopupClose.bind(this)}>
                    <Modal.Header closeButton>
                        <Modal.Title>Claim History</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Table striped bordered hover>
                            <thead>
                            <tr>
                                <th>#</th>
                                <th>Received Date</th>
                                <th>Open</th>
                            </tr>
                            </thead>
                            {
                                this.state.historyClaims.map((claim) => (
                                    <tr>
                                        <td> {claim.id} </td>
                                        <td> {moment(claim.masterReceivedDate, "YYYY-MM-DD").format("MM/DD/YYYY")} </td>
                                        <td> <Button data-claimId={claim.id} onClick={this.openHistoryClaim.bind(this)}> Open </Button> </td>
                                    </tr>
                                ))
                            }
                        </Table>
                    </Modal.Body>
                </Modal>
            </div>

        );
    }
}

export default CreateClaim;