import React from 'react';

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Alert from 'react-bootstrap/Alert';

import { AgGridReact } from '@ag-grid-community/react';
import { AllModules } from '@ag-grid-enterprise/all-modules';

import { BiPlusCircle } from "react-icons/bi";
import { MdFileDownload } from 'react-icons/md';

import ApiService from './../../service/APIService';
import { Container } from 'react-bootstrap';

import AgGridActionColumnRenderer from './AgGridActionColumnRenderer'

import "./style.css";

class MasterDataEdit extends React.Component {

    apiService = new ApiService();
    apiEndPointMap = {
        "coveragetypemaster" : "updatecoveragetypemaster",
        "coveragetyperule" : "updatecoveragetyperule",
        "networkmaster" : "updatenetworkmaster",
        "networkrule" : "updatenetworkrule",
        "claimtypemaster" : "updateclaimtypemaster",
        "claimtyperule" : "updateclaimtyperule",
        "exclusioncodemaster" : "updateexclusioncodemaster",
        "exclusioncoderule" : "updateexclusioncoderule",
        "statustypemaster" : "updatestatustypemaster",
        "statustyperule" : "updatestatustyperule",
        "supervisormaster" : "updatesupervisormaster",
        "supervisorrule" : "updatesupervisorrule",
        "eligibilityoverridereasonmaster" : "updateeligibilityoverridereasonmaster",
        "eligibilityoverridereasonrule" : "updateeligibilityoverridereasonrule",
        "reasonforholdingclaimmaster" : "updatereasonforholdingclaimmaster",
        "reasonforholdingclaimrule" : "updatereasonforholdingclaimrule",
        "adjudicationsequencemaster" : "updateadjudicationsequencemaster",
        "adjudicationsequencerule" : "updateadjudicationsequencerule",
        "providermaster" : "updateprovidermaster",
        "providerrule" : "updateproviderrule",
        "providerstatustypemaster" : "updateproviderstatustypemaster",
        "providerstatustyperule" : "updateproviderstatustyperule",
        "cptmaster" : "updatecptmaster",
        "cptrule" : "updatecptrule",
        "specialprovidercptmaster" : "updatespecialprovidercptmaster",
        "specialprovidercptrule" : "updatespecialprovidercptrule",
        "specialclaimtypecptmaster" : "updatespecialclaimtypecptmaster",
        "specialclaimtypecptrule" : "updatespecialclaimtypecptrule",
        "icd10master" : "updateicd10master",
        "icd10rule" : "updateicd10rule",
        "proceduremaster" : "updateproceduremaster",
        "procedurerule" : "updateprocedurerule",
        "procedurelimitsused" : "updateprocedurelimitsused",
        "limitsmaster" : "updatelimitsmaster",
        "limitsused" : "updatelimitsused"
    }

    constructor(props) {

        super(props);

        this.label = this.props.match.params.label;

        this.state = {
            item : "",
            rowData: [],
            label: this.label,
            id: "",
            parentFields: [],
            childField: "",
            childRows: [],
            childCols: [],
            childClass: "",
            formData: {},
            childLabel: "",

            agGridParams: "",

            gridApi: "",
            columnAPI: "",

            alertMessage: "",
            varient: "",
            showNotification: false,

            createChildLink: "",

            context: { componentParent: this },
            frameworkComponents: {
                gridActionColumnRenderer : AgGridActionColumnRenderer,
            },

            defaultColDef: {
                sortable: true,
                editable: true,
                filter: 'agTextColumnFilter',
                floatingFilter: true,
                resizable: true,
            },

            modules: AllModules,
            sideBar: {
                toolPanels: [
                  {
                    id: 'columns',
                    labelDefault: 'Columns',
                    labelKey: 'columns',
                    iconKey: 'columns',
                    toolPanel: 'agColumnsToolPanel',
                    toolPanelParams: {
                        suppressRowGroups: true,
                        suppressValues: true,
                        suppressPivots: true,
                        suppressPivotMode: true,
                        suppressSideButtons: true,
                        suppressColumnFilter: true,
                        suppressColumnSelectAll: true,
                        suppressColumnExpandAll: true,
                      },
                  },
                ],
                defaultToolPanel: '',
            }
        }

    }

    componentDidMount() {
        this.fetchMasterDataItemFormFields();
    }

    export() {

        var exportCols = [];
        for(var i=0; i< this.state.childCols.length; i++)
        {
            if(this.state.childCols[i].field != "action" && !this.state.childCols[i].hide)
                exportCols.push(this.state.childCols[i].field);
        }

        var root = this;
        this.state.gridAPI.exportDataAsCsv({
            columnKeys: exportCols,
            //onlySelected: true,
            allColumns: false,
            fileName: root.state.childLabel + " List.csv"
        });

        //this.state.gridAPI.exportDataAsCsv({});
    }

    editMasterItem() {

        //validation

        //1. Required fields
        for(var i=0; i< this.state.parentFields.length; i++) {
            if(this.state.parentFields[i].hidden != 'y' && this.state.parentFields[i].required == 'y') {

                if(this.state.formData[this.state.parentFields[i].identifier].toString().trim().length == 0) {
                    this.setState({
                        alertMessage: this.state.parentFields[i].label + " is a required field",
                        varient: "danger",
                        showNotification: true
                    })
                    var root = this;
                    setTimeout(function() {
                        root.setState({
                            showNotification: false
                        })
                    }, 4000)
                    return;
                }
            }
        }
        //end validation
        
        var ep = this.apiEndPointMap[this.state.item.toLowerCase()];

        var url;
        if(this.state.cid == undefined) //means master item
            url = this.apiService.getApiUrl("editMasterItem", [ep]);
        else 
            url = this.apiService.getApiUrl("editMasterChildItem", [ep, this.state.id]);

        console.log(this.state.formData);

        var root = this;
        this.apiService.invokePut(url, JSON.stringify(this.state.formData), this, function(response){
            if(response.status == 'success') {
                root.setState({
                    alertMessage: "Successfully updated " + root.state.label,
                    varient: "success",
                    showNotification: true
                })

                setTimeout(function() {
                    root.setState({
                        showNotification: false
                    })
                }, 4000)
            } else if(response.status == 'error') {
                var error = "";
                if(response.message.indexOf("duplicate key error collection") > 0)
                    error = "Same CPT number is already exists";

                root.setState({
                    alertMessage: "Error during saving " + root.state.label + " : " + error,
                    varient: "danger",
                    showNotification: true
                })

                setTimeout(function() {
                    root.setState({
                        showNotification: false
                    })
                }, 4000)
            }
        })
    }

    fetchMasterDataItemFormFields() {

        var item = this.props.match.params.item;
        var id = this.props.match.params.id;

        //only for child edit
        //child edit URL pattern
        ///portal/masterdataedit/:item?/masteritem/:masteritem?/label/:label?/id/:id?/cid/:cid?
        var cid = this.props.match.params.cid;     //child id
        var masterItem = this.props.match.params.masteritem;

        if(item == undefined || id == undefined) return;
        this.setState({
            'item' : item,
            'id' : id,
            'cid' : cid,
            'masterItem' : masterItem
        })

        if(cid != undefined) //merans child edit
        {
            //if child item edit, I could not able to brind the label via URl due to some react routing issue
            this.label = this.constructLabelFromItem(item);
            this.setState({
                'label' : this.label
            })
        }

        var url = this.apiService.getApiUrl("getmasterItemFormFields", [item]);
        var root = this;
        this.apiService.invokeGet(url, this, function(response){

            console.log(response);

            var constructedChildLabel = "";
            var childField = response.data.childField;

            //for child items edit child field will be null, so check it first
            if(childField != null) {
                constructedChildLabel = root.constructLabelFromItem(childField);
            }

            root.setState({
                parentFields : response.data.parent,
                childField : response.data.childField,
                childClass : response.data.childClass,
                childLabel: constructedChildLabel,
                createChildLink: "/portal/masterdataentry/" + response.data.childClass + "/label/" + constructedChildLabel + "/mid/" + root.state.id
            })
            var obj = {};
            
            for(var i=0; i< response.data.parent.length; i++) {
                obj[response.data.parent[i].identifier] = ""
            }

            //add child field
            obj[response.data.childField] = "";

            root.setState({formData : obj})

            root.getDataById();
        })
    }

    constructLabelFromItem(item) {
        var label = "";
        for(var i=0; i< item.length; i++) {
            if(i == 0)label += item[i].toUpperCase();
            else if(this.isCaps(item[i]))label += ' ' + item[i];
            else label += item[i]
        }
        return label;
    }

    isCaps(ch) {
        if (!isNaN(ch * 1)){
           return 'ch is numeric';
        }
         else {
           if (ch == ch.toUpperCase()) {
              return true;
           }
           if (ch == ch.toLowerCase()){
              return false;
           }
        }
    }

    getDataById() {

        var root = this;
        var url = "";
        
        if(this.state.cid == undefined) //means parent item
            url = this.apiService.getApiUrl("getMasterItemById", [this.state.item, this.state.id]);
        else 
            url = this.apiService.getApiUrl("getMasterChildItemById", [this.state.item, this.state.id, this.state.cid]); //for child item this.state.item indicates child

        this.apiService.invokeGet(url, this, function(response){

            var data = response.data;
            var obj = {};
            var childRows;
            for (var key in root.state.formData) {

                obj[key] = data[key];
            }

            root.setState({formData: obj})

            if(root.state.cid == undefined) //means parent item
                root.getChildTableFields(data[root.state.childField]);
        })
    }

    getChildTableFields(childRows) {
        var root = this;
        var url = this.apiService.getApiUrl("getTableFields", [this.state.childClass]);

        this.apiService.invokeGet(url, this, function(response){

            var data = response.data;
            var tableFields = data.tableFields;
            var colDefs = [];
            var rowDefs = [];
            console.log(childRows);
            for(var i=0; i< tableFields.length; i++) {
                //after first 4 columns hide the rest
                if(i < 4){
                    if(tableFields[i].hidden == 'y')
                        colDefs.push({ headerName: tableFields[i].label, field: tableFields[i].identifier, hide: true })
                    else
                        colDefs.push({ headerName: tableFields[i].label, field: tableFields[i].identifier})
                }
                else colDefs.push(
                    { 
                        headerName: tableFields[i].label, 
                        field: tableFields[i].identifier,
                        hide: true,
                        suppressToolPanel: true
                    }
                )
            }

            //extra column for actions like delete
            var obj = {};
            obj.headerName = "Action"
            obj.field = "action"
            obj.cellRenderer = 'gridActionColumnRenderer'
            colDefs.push(obj);

            for(var i=0; i< childRows.length; i++) {
                var row = childRows[i];
                
                //BAD CODING!!! Need to check at Server side.. for quick delivery
                if(row.isDeleted)continue;
                var rowObj = {};

                for(var j=0; j< tableFields.length; j++) {

                    if(row[tableFields[j].identifier] == null) {
                        rowObj[tableFields[j].identifier] = "";
                    }
                    else {
                        var value = row[tableFields[j].identifier].toString();

                        //check if date => match pattern yyyy-mm-dd
                        if(value.match(/^\d{4}\-(0[1-9]|1[012])\-(0[1-9]|[12][0-9]|3[01])$/))
                        {
                            var parts = value.split("-");
                            value = parts[1] + "/" + parts[2] + "/" + parts[0];
                        }
                        rowObj[tableFields[j].identifier] = value;
                    }
                }
                rowObj.action = "delete";
                if(!rowObj.isDeteled)rowDefs.push(rowObj);
            }
            
            //console.log(colDefs);
            //console.log(rowDefs);
            root.setState({
                childCols: colDefs,
                childRows: rowDefs
            }, function() {})
        })
    }

    onFirstDataRendered(params) {
        params.api.sizeColumnsToFit();
    }

    commonChangeHandler(event) {
        var fdata = this.state.formData;
        fdata[event.target.dataset.name] = event.target.value
        this.setState({formData : fdata})
    }

    goToListItems() {
        if(this.state.cid == undefined) //means parent item
            window.location.href = "/portal/masteritem/" + this.state.item + "/label/" + this.state.label;
        else window.history.back();
    }

    onGridReady(params) {
        this.setState({
            gridAPI: params.api,
            columnAPI: params.columnApi
        })
    }

    onGridSizeChanged(params) {
        params.api.sizeColumnsToFit();
    }

    delete(rowdata, rowIndex) {

        var id = rowdata.id;
        console.log([this.state.item, this.state.id, this.state.childField, id]);
        var url = this.apiService.getApiUrl(
            "deleteChildItemById", 
            [this.state.item, this.state.id, this.state.childField, id]
        );

        var root = this;
        this.apiService.invokeDelete(url, {}, this, function(response) {
            if(response.status == "success") {
                var tableData = root.state.childRows;
                tableData.splice(rowIndex, 1);
                root.state.gridAPI.setRowData(tableData);
            } else {
                root.state.alertMessage = "Error during deleting the record. Please contact System administrator."
            }
        })
    }

    onCellClicked(params) {
        //console.log(params.data.id);
        if(params.column.colDef.field == "action") return;

        var item = this.state.item;
        var childLabel = this.state.childLabel;
        ///portal/masterdataedit/:item?/label/:label?/id/:id?
        //window.location.href = "/portal/masterdataedit/" + this.state.childClass + "/masteritem/" + item + "/label/" + childLabel + "/id/" + this.state.id + "/cid/" + params.data.id;
        window.location.href = "/portal/masterdataedit/" + this.state.childClass + "/id/" + this.state.id + "/cid/" + params.data.id;
    }

    render() {

        var root = this;
        return (
            <div className="list-container">
                <Row className="headerRow">
                    <Col>
                        <span className="h3-header"> Edit { this.label } </span>
                    </Col>

                    <Col>
                        <Button className="claim-dashboard-btn mateffect" variant="primary" onClick={this.goToListItems.bind(this)}> 
                            { this.label } List
                        </Button>
                    </Col>
                </Row>

                <Container fluid>
                    <Alert key="alert" variant={this.state.varient} className={(this.state.showNotification ? 'show' : 'hidden')}>
                        {this.state.alertMessage}
                    </Alert>
                    <Form.Group as={Row}>
                    {
                        this.state.parentFields.map(function(field, index) {
                            if(field.type == "number")
                                return (
                                    <Col md={field.size} sm={field.size} className={(field.hidden == 'n' ? 'show' : 'hidden')}>
                                        <Form.Group as={Row} className="field-group">
                                            <Form.Label className={(field.required == 'y' ? 'required' : 'notrequired')}> 
                                                {field.label} 
                                            </Form.Label>
                                            <Form.Control 
                                                key={field.identifier}
                                                type="text"
                                                value={root.state.formData[field.identifier] || ""} 
                                                data-name={field.identifier}
                                                data-type={field.type}
                                                maxLength={field.datalength}
                                                onChange={root.commonChangeHandler.bind(root)}
                                            />
                                        </Form.Group>
                                    </Col>
                                )
                            else
                                return (
                                    <Col md={field.size} sm={field.size} className={(field.hidden == 'n' ? 'show' : 'hidden')}>
                                        <Form.Group as={Row} className="field-group">
                                            <Form.Label className={(field.required == 'y' ? 'required' : 'notrequired')}> 
                                                {field.label} 
                                            </Form.Label>
                                            <Form.Control 
                                                key={field.identifier}
                                                type={field.type}
                                                value={root.state.formData[field.identifier] || ""} 
                                                data-name={field.identifier}
                                                data-type={field.type} 
                                                maxLength={field.datalength}
                                                onChange={root.commonChangeHandler.bind(root)}
                                            />
                                        </Form.Group>
                                    </Col>
                                )
                        })
                    }
                    </Form.Group>
                    
                    <Row>
                        <Col>
                            <Button className="claim-dashboard-btn mateffect" variant="primary" onClick={this.editMasterItem.bind(this)}> 
                                Update
                            </Button>
                        </Col>
                    </Row>

                    <hr />

                    <Row className={((this.state.cid == undefined) ? 'show' : 'hidden')}>
                        <Col md={10}>
                            <span className="h3-header"> {this.state.childLabel} </span>
                        </Col>

                        <Col md={2}>
                            <a className="add-child-link" href={this.state.createChildLink}> 
                                <BiPlusCircle />&nbsp;
                                Add new {this.state.childLabel} 
                            </a>
                        </Col>
                    </Row>

                    <Row className={((this.state.cid == undefined) ? 'show' : 'hidden')}>
                        <div id="grid-wrapper-child-item-list" style={{ width: '100%', height: '100%' }}>
                            <div className="ag-theme-alpine ag-grid-container" style={ {height: '300px', width: '100%'} }>
                                <AgGridReact
                                    columnDefs={this.state.childCols}
                                    rowData={this.state.childRows}
                                    context={this.state.context}
                                    modules={this.state.modules}
                                    sideBar={this.state.sideBar}
                                    defaultColDef={this.state.defaultColDef}
                                    frameworkComponents={this.state.frameworkComponents}
                                    onGridSizeChanged={this.onGridSizeChanged.bind(this)}
                                    onCellClicked={this.onCellClicked.bind(this)}
                                    onFirstDataRendered={this.onFirstDataRendered.bind(this)}
                                    onGridReady={this.onGridReady.bind(this)}
                                    
                                >
                                </AgGridReact>
                            </div>
                            <div class="tbl-footer">
                                <MdFileDownload className="export-icon" size='1.5em' onClick={this.export.bind(this)}/>
                            </div>
                        </div>
                    </Row>
                </Container>

                <br />
                <br />
                <br />
            </div>
        )
    }

    onGridSizeChanged = params => {

        /*this.setState({agGridParams: params});

        var gridWidth = document.getElementById('grid-wrapper-child-item-list').offsetWidth;
        var columnsToShow = [];
        var columnsToHide = [];
        var totalColsWidth = 0;
        var allColumns = params.columnApi.getAllColumns();
        for (var i = 0; i < allColumns.length; i++) {
          var column = allColumns[i];
          totalColsWidth += column.getMinWidth();
          if (totalColsWidth > gridWidth) {
            columnsToHide.push(column.colId);
          } else {
            columnsToShow.push(column.colId);
          }
        }
        params.columnApi.setColumnsVisible(columnsToShow, true);
        params.columnApi.setColumnsVisible(columnsToHide, false);
        params.api.sizeColumnsToFit();*/
    };
}

export default MasterDataEdit;