import React from 'react';

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';

import {AgGridReact} from 'ag-grid-react';

import 'ag-grid-enterprise';
import { SetFilterModule } from '@ag-grid-enterprise/set-filter';

import ApiService from './../../service/APIService';

import "./style.css";

class MasterDataList extends React.Component {

    apiService = new ApiService();
    labelNameMap = {};

    constructor(props) {

        super(props);

        this.state = {
            columnDefs: [
                  { headerName: "Master Items", field: "masteritem", filter: true, floatingFilter: true, flex: 1 }
              ],
              rowData: [
                  
              ],

              modules: [
                SetFilterModule
              ]
        }

        this.fetchMasterItemList();
    }

    goToDashBoard() {
        window.location.href = "/portal/dashboard";
    }

    fetchMasterItemList() {

        var url = this.apiService.getApiUrl("getMasterItems");
        var root = this;
        this.apiService.invokeGet(url, this, function(response){

            var data = response.data;
            var rows = [];
            for(var i=0; i< data.length; i++) {
                var obj = {};
                obj['masteritem'] = data[i]['label'];
                root.labelNameMap[data[i]['label']] = data[i]['identifier'];

                rows.push(obj);
            }
            root.setState({rowData : rows});
        })
    }

    onCellClicked(obj) {
        console.log(obj);
        var cellVal = obj.data.masteritem;
        var identifier = this.labelNameMap[cellVal];
        window.location.href = "/portal/masteritem/" + identifier + "/label/" + cellVal;
    }

    render() {
        return (
            <div className="list-container">
                <Row className="headerRow">
                    <Col>
                        <span className="h3-header"> Master Data List </span>
                    </Col>

                    <Col>
                        <Button className="claim-dashboard-btn mateffect" variant="primary" onClick={this.goToDashBoard.bind(this)}> 
                            Go To Dashboard
                        </Button>
                    </Col>
                </Row>

                <Row>
                    <div id="grid-wrapper-master-item-list" style={{ width: '100%', height: '100%' }}>
                        <div className="ag-theme-alpine ag-grid-container" style={ {height: '400px', width: '100%'} }>
                            <AgGridReact
                                modules={this.state.modules}
                                columnDefs={this.state.columnDefs}
                                rowData={this.state.rowData}
                                frameworkComponents={this.state.frameworkComponents}
                                onGridSizeChanged={this.onGridSizeChanged.bind(this)}
                                onCellClicked={this.onCellClicked.bind(this)}
                                
                            >
                            </AgGridReact>
                        </div>
                    </div>
                </Row>
            </div>
        )
    }

    onGridSizeChanged = params => {
        var gridWidth = document.getElementById('grid-wrapper-master-item-list').offsetWidth;
        var columnsToShow = [];
        var columnsToHide = [];
        var totalColsWidth = 0;
        var allColumns = params.columnApi.getAllColumns();
        for (var i = 0; i < allColumns.length; i++) {
          var column = allColumns[i];
          totalColsWidth += column.getMinWidth();
          if (totalColsWidth > gridWidth) {
            columnsToHide.push(column.colId);
          } else {
            columnsToShow.push(column.colId);
          }
        }
        params.columnApi.setColumnsVisible(columnsToShow, true);
        params.columnApi.setColumnsVisible(columnsToHide, false);
        params.api.sizeColumnsToFit();
    };
}

export default MasterDataList;