import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'; 

import KMRClaims from './KMRClaims';
import CreateClaim from './create-claim/CreateClaim';
import MasterDataList from './master-data-list/MasterDataList';

import "../styles/KMRContentArea.css";
import MasterDataItem from './master-data-item/MasterDataItem';
import MasterDataEntry from './master-data-entry/MasterDataEntry';
import MasterDataEdit from './master-data-edit/MasterDataEdit';


class KMRContentArea extends React.Component {

    render() {
        return (
            <div className="fixed-content-area container-fluid">
                <Router>
                    <Route exact path='/portal/dashboard' component={KMRClaims}></Route> 
                    <Route exact path='/portal/create/:claimid?' component={CreateClaim}></Route> 
                    <Route exact path='/portal/masterdata' component={MasterDataList}></Route> 
                    <Route exact path='/portal/masteritem/:item?/label/:label?' component={MasterDataItem}></Route> 
                    {/* For Master Data Entry */}
                    <Route exact path='/portal/masterdataentry/:item?/label/:label?' component={MasterDataEntry}></Route> 
                    {/* For Child Data Entry*/}
                    <Route exact path='/portal/masterdataentry/:item?/label/:label?/mid/:mid?' component={MasterDataEntry}></Route> 

                    {/* For Master Item Edit */}
                    <Route exact path='/portal/masterdataedit/:item?/label/:label?/id/:id?/:cid?' component={MasterDataEdit}></Route>
                    
                    {/* For Child Item Edit : cid => child id*/}
					<Route exact path='/portal/masterdataedit/:item?/id/:id?/cid/:cid?' component={MasterDataEdit}></Route> 
                    
                </Router>
            </div>
        );
    }
}

export default KMRContentArea;