import React from 'react';

import {AgGridReact} from 'ag-grid-react';
import AgGridStatusBadgeRenderer from './AgGridStatusBadgeRenderer';

class KMRPendingInfoTabContent extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
          columnDefs: [
                { headerName: "Claim Id", field: "id" },
                { headerName: "Member Name", field: "name" },
                { headerName: "Member SSN", field: "ssn" },
                { headerName: "Last Update", field: "date" },
                { headerName: "Status", field: "status", cellRenderer: 'gridStatusBadgeRenderer' }
            ],
            rowData: [
                { id: "1234", name: "Kevin Peterson", ssn: 42342421, date: "01/12/2019", status: "Pending Info" },
                { id: "3452", name: "Jack Ryan", ssn: 65344634, date: "05/03/2020", status: "Pending Info" },
                { id: "1221", name: "Rob Stark", ssn: 546343123, date: "06/07/2019", status: "Pending Info" },
                { id: "3221", name: "Ned Stark", ssn: 86786545, date: "12/12/2020", status: "Pending Info" },
                { id: "5676", name: "Denis Lilly", ssn: 78643456, date: "12/09/2019", status: "Pending Info" },
                { id: "8675", name: "Michael Vaughn", ssn: 86754346, date: "03/12/2019", status: "Pending Info" },
                { id: "9785", name: "John Snow", ssn: 32424523, date: "30/12/2019", status: "Pending Info" },
                { id: "5345", name: "Tom Clancy", ssn: 3453456, date: "31/08/2019", status: "Pending Info" },
                { id: "5945", name: "Michel Stark", ssn: 86786789, date: "12/08/2019", status: "Pending Info" },
                { id: "5676", name: "Steve Smith", ssn: 5675445, date: "12/09/2019", status: "Pending Info" },
                { id: "9786", name: "John McNally", ssn: 67896789, date: "12/10/2019", status: "Pending Info" },
                { id: "5988", name: "Karl Hooper", ssn: 234345, date: "12/12/2019", status: "Pending Info" },
                { id: "5343", name: "Tyrion Lanister", ssn: 89076789, date: "12/12/2019", status: "Pending Info" },
                { id: "9767", name: "Brian Smith", ssn: 58567857, date: "12/12/2019", status: "Pending Info" },
                { id: "2218", name: "Cerci Lanister", ssn: 423467, date: "12/12/2019", status: "Pending Info" }
            ],
            frameworkComponents: {
                gridStatusBadgeRenderer: AgGridStatusBadgeRenderer,
            }
        }
    }

    render() {
        return (
            <div id="grid-wrapper-pending-info" style={{ width: '100%', height: '100%' }}>
                <div className="ag-theme-alpine ag-grid-container" style={ {height: '300px', width: '100%'} }>
                    <AgGridReact
                        columnDefs={this.state.columnDefs}
                        rowData={this.state.rowData}
                        frameworkComponents={this.state.frameworkComponents}
                        onGridSizeChanged={this.onGridSizeChanged.bind(this)}
                        
                    >
                    </AgGridReact>
                </div>
            </div>
        );
    }

    

    onGridSizeChanged = params => {
        var gridWidth = document.getElementById('grid-wrapper-pending-info').offsetWidth;
        var columnsToShow = [];
        var columnsToHide = [];
        var totalColsWidth = 0;
        var allColumns = params.columnApi.getAllColumns();
        for (var i = 0; i < allColumns.length; i++) {
          var column = allColumns[i];
          totalColsWidth += column.getMinWidth();
          if (totalColsWidth > gridWidth) {
            columnsToHide.push(column.colId);
          } else {
            columnsToShow.push(column.colId);
          }
        }
        params.columnApi.setColumnsVisible(columnsToShow, true);
        params.columnApi.setColumnsVisible(columnsToHide, false);
        params.api.sizeColumnsToFit();
    };
}

export default KMRPendingInfoTabContent;