import React from 'react';

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';

import AgGridActionColumnRenderer from './AgGridActionColumnRenderer'

import { AgGridReact } from '@ag-grid-community/react';
import { AllModules } from '@ag-grid-enterprise/all-modules';

import { MdFileDownload } from 'react-icons/md';

import ApiService from './../../service/APIService';

import "./style.css";

class MasterDataItem extends React.Component {

    apiService = new ApiService();
    itemSpecificProperties = {
        "CoverageTypeMaster" : {
            "id" : "Coverage Type Id",
            "hiddenCols" : ["Coverage Type Id" ]
        },

        "NetworkMaster" : {
                "id" : "Network Id",
                "hiddenCols" : ["Network Id" ]
        },

        "ClaimTypeMaster" : {
                "id" : "Claim Type Id",
                "hiddenCols" : ["Claim Type Id" ]
        },

        "ExclusionCodeMaster" : {
                "id" : "Exclusion Code Id",
                "hiddenCols" : ["Exclusion Code Id" ]
        },

        "StatusTypeMaster" : {
                "id" : "Status Type Id",
                "hiddenCols" : ["Status Type Id" ]
        },

        "SupervisorMaster" : {
                "id" : "Supervisor Id",
                "hiddenCols" : ["Supervisor Id" ]
        },

        "EligibilityOverrideReasonMaster" : {
                "id" : "Eligibility Override Reason Id",
                "hiddenCols" : ["Eligibility Override Reason Id" ]
        },

        "ReasonforHoldingClaimMaster" : {
                "id" : "Reason for Holding Claim Id",
                "hiddenCols" : ["Reason for Holding Claim Id" ]
        },

        "AdjudicationSequenceMaster" : {
                "id" : "Adjudication Sequence Id",
                "hiddenCols" : ["Adjudication Sequence Id" ]
        },

        "ProviderMaster" : {
                "id" : "Provider Id",
                "hiddenCols" : ["Provider Id" , "Address 2","State","Zip","Zip4","Telephone","Foreign","Hospital/Office Code","Comments","Tax ID#","Provider Type"]
        },

        "ProviderStatusTypeMaster" : {
                "id" : "Provider Status Type Id",
                "hiddenCols" : ["Provider Status Type Id" ]
        },

        "CPTMaster" : {
                "id" : "CPT Id",
                "hiddenCols" : ["CPT Id" ]
        },

        "SpecialProviderCPTMaster" : {
                "id" : "Special Provider CPT Id",
                "hiddenCols" : ["Special Provider CPT Id" , "Description"]
        },

        "SpecialClaimTypeCPTMaster" : {
                "id" : "Special Claim Type CPT Id",
                "hiddenCols" : ["Special Claim Type CPT Id" , "Description"]
        },

        "ICD10Master" : {
                "id" : "ICD-10 Id",
                "hiddenCols" : ["ICD-10 Id" ]
        },

        "ProcedureMaster" : {
                "id" : "Procedure Id",
                "hiddenCols" : ["Procedure Id" ]
        },

        "LimitsMaster" : {
                "id" : "Limits Id",
                "hiddenCols" : ["Limits Id" , "Medical Lifetime Limit for Coverage Type","Dental Family Coverage for Coverage Type","Dental Yearly Limit for Coverage Type","Dental Lifetime Limit for Coverage Type","Prescription Family Coverage for Coverage Type","Prescription Yearly Limit for Coverage Type","Prescription Lifetime Limit for Coverage Type","Track Basic Limits for Coverage Type","Basic Yearly Limit for Coverage Type","Basic Lifetime Limit for Coverage Type","Track Major Medical Limits for Coverage Type","Major Medical Yearly Limit for Coverage Type","Major Medical Lifetime Limit for Coverage Type","Member Yearly Deductible","Member Lifetime Deductible","Spouse Yearly Deductible","Spouse Lifetime Deductible","Dependent Yearly Deductible","Dependent Lifetime Deductible","Family Yearly Deductible","Family Lifetime Deductible","Member Co-Pay","Spouse Co-Pay","Dependent Co-Pay","Family Co-Pay"]
        }
    };

    constructor(props) {

        super(props);

        this.label = this.props.match.params.label;

        this.state = {
            columnDefs: [{}],
            rowData: [{}],
            label: this.label,
            item: "",
            gridAPI: null,
            columnAPI: null,
            alertMessage: "",
            context: { componentParent: this },
            rowSelection: 'multiple',

            frameworkComponents: {
                gridActionColumnRenderer : AgGridActionColumnRenderer,
            },

            defaultColDef: {
                sortable: true,
                editable: true,
                filter: 'agTextColumnFilter',
                floatingFilter: true,
                resizable: true,
                flex: 1,
                width: 200,
            },

            modules: AllModules,
            sideBar: {
                toolPanels: [
                  {
                    id: 'columns',
                    labelDefault: 'Columns',
                    labelKey: 'columns',
                    iconKey: 'columns',
                    toolPanel: 'agColumnsToolPanel',
                    toolPanelParams: {
                        suppressRowGroups: true,
                        suppressValues: true,
                        suppressPivots: true,
                        suppressPivotMode: true,
                        suppressSideButtons: true,
                        suppressColumnFilter: true,
                        suppressColumnSelectAll: true,
                        suppressColumnExpandAll: true,
                      },
                  },
                ],
                defaultToolPanel: '',
            }
        }

    }

    componentDidMount() {
        this.fetchMasterDataItem();
    }

    goToCreateMasterItem() {
        window.location.href = "/portal/masterdataentry/" + this.state.item + "/label/" + this.state.label;
    }

    goToGlobalMasterItemList() {
        window.location.href = "/portal/masterdata";
    }

    fetchMasterDataItem() {

        var item = this.props.match.params.item;

        if(item == undefined) return;
        this.setState({'item': item});


        var url = this.apiService.getApiUrl("getmasterSpecificMasterItemList", [this.props.match.params.item]);
        var root = this;
        this.apiService.invokeGet(url, this, function(response){

            var data = response.data;
            
            var cols = [];
            if(data.length > 0) {

                //modify data (date format/null checking etc.)
                for(var x = 0; x < data.length; x++) {

                    for (var key in data[x]) {
                        if(data[x][key] == null) {
                            data[x][key] = "";
                        }
                        else {
                            var value = data[x][key].toString();

                            //check if date => match pattern yyyy-mm-dd
                            if(value.match(/^\d{4}\-(0[1-9]|1[012])\-(0[1-9]|[12][0-9]|3[01])$/))
                            {
                                var parts = value.split("-");
                                value = parts[1] + "/" + parts[2] + "/" + parts[0];
                            }
                            data[x][key] = value;
                        }
                    }
                }

                var isFirstVisibleCol = true;
                var visibleColumnCount = 0;
                for (var key in data[0]) {
                    var obj = {}
                    obj.headerName = key;
                    obj.field = key;

                    if(root.itemSpecificProperties[root.state.item].hiddenCols.indexOf(obj.field) >= 0) {
                        obj.hide = true;
                        obj.suppressToolPanel = true;
                    } else {
                        //for first visible row add checkbox selection
                        if(isFirstVisibleCol) {
                            obj.checkboxSelection = true;
                            obj.headerCheckboxSelection =  true;
                            isFirstVisibleCol = false;
                        }

                        //after first 4 columns hide the rest
                        if(visibleColumnCount > 4) {
                            obj.hide = true;
                            obj.suppressToolPanel = true;
                        }
                        visibleColumnCount++;
                    }
                    cols.push(obj);
                }
            }
            
            //extra column for actions like delete
            var obj = {};
            obj.headerName = "Action"
            obj.field = "action"
            obj.cellRenderer = 'gridActionColumnRenderer'
            cols.push(obj);

            for(var i=0; i< data.length; i++)data[i].action = "delete";

            console.log(cols);

            root.setState({
                columnDefs: cols,
                rowData : data
            }, function(){
               
            });
        })
    }

    componentDidUpdate() {
        
    }

    delete(rowdata, rowIndex) {
        var idField = this.itemSpecificProperties[this.state.item].id;
        var id = rowdata[idField];
        var url = this.apiService.getApiUrl("deleteMasterItemById", [this.state.item, id]);

        var root = this;
        this.apiService.invokeDelete(url, {}, this, function(response) {
            if(response.status == "success") {
                var tableData = root.state.rowData;
                tableData.splice(rowIndex, 1);
                root.state.gridAPI.setRowData(tableData);
            } else {
                root.state.alertMessage = "Error during deleting the record. Please contact System administrator."
            }
        })
    }

    onGridReady(params) {
        this.setState({
            gridAPI: params.api,
            columnAPI: params.columnApi
        })
    }

    onFirstDataRendered(params) {
        var root = this;
        params.api.sizeColumnsToFit();
    }

    onCellClicked(obj) {
        //console.log(obj);
        //for action item click, it should not redirect to edit page
        if(obj.column.colDef.field == "action") return;
        var identifier = obj.data[this.itemSpecificProperties[this.state.item].id];
        window.location.href = "/portal/masterdataedit/" + this.state.item + "/label/" + this.state.label + "/id/" + identifier;
    }

    onRowDataChange(params) {
        
    }

    exportCsv() {
        //console.log(this.state.columnDefs);
        var exportCols = [];
        for(var i=0; i< this.state.columnDefs.length; i++)
        {
            if(this.state.columnDefs[i].field != "action")exportCols.push(this.state.columnDefs[i].field);
        }

        var root = this;
        this.state.gridAPI.exportDataAsCsv({
            columnKeys: exportCols,
            onlySelected: true,
            allColumns: false,
            fileName: root.label + " List.csv"
        });
    }

    render() {
        return (
            <div className="list-container">
                <Row className="headerRow">
                    <Col>
                        <span className="h3-header"> { this.label } List </span>
                    </Col>

                    <Col>
                        <Button className="claim-dashboard-btn mateffect" variant="primary" onClick={this.goToGlobalMasterItemList.bind(this)}> 
                            Master Item List
                        </Button>

                        <Button className="claim-dashboard-btn mateffect margin-right" variant="primary" onClick={this.goToCreateMasterItem.bind(this)}> 
                            Create { this.label }
                        </Button>
                    </Col>
                </Row>

                <Row>

                    <Alert key="alert" variant="danger" className={(this.state.showNotification ? 'show' : 'hidden')}>
                        {this.state.alertMessage}
                    </Alert>

                    <div id="grid-wrapper-master-data-item" style={{ width: '100%', height: '100%' }}>
                        <div className="ag-theme-alpine ag-grid-container" style={ {height: '400px', width: '100%'} }>
                            <AgGridReact
                                columnDefs={this.state.columnDefs}
                                rowData={this.state.rowData}
                                context={this.state.context}
                                modules={this.state.modules}
                                sideBar={this.state.sideBar}
                                rowSelection={this.state.rowSelection}
                                defaultColDef={this.state.defaultColDef}
                                frameworkComponents={this.state.frameworkComponents}
                                onGridSizeChanged={this.onGridSizeChanged.bind(this)}
                                onCellClicked={this.onCellClicked.bind(this)}
                                onFirstDataRendered={this.onFirstDataRendered.bind(this)}
                                onGridReady={this.onGridReady.bind(this)}
                                onRowDataChanged={this.onRowDataChange.bind(this)}
                                
                            >
                            </AgGridReact>
                        </div>
                        <div class="tbl-footer">
                            <MdFileDownload className="export-icon" size='1.5em' onClick={this.exportCsv.bind(this)}/>
                        </div>
                    </div>
                </Row>
            </div>
        )
    }

    onGridSizeChanged(params) {
        /*var gridWidth = document.getElementById('grid-wrapper-master-data-item').offsetWidth;
        var columnsToShow = [];
        var columnsToHide = [];
        var totalColsWidth = 0;
        var allColumns = params.columnApi.getAllColumns();
        for (var i = 0; i < allColumns.length; i++) {
          var column = allColumns[i];
          totalColsWidth += column.getMinWidth();
          if (totalColsWidth > gridWidth) {
            columnsToHide.push(column.colId);
          } else {
            columnsToShow.push(column.colId);
          }
        }
        params.columnApi.setColumnsVisible(columnsToShow, true);
        params.columnApi.setColumnsVisible(columnsToHide, false);
        params.api.sizeColumnsToFit();*/
    }
}

export default MasterDataItem;