import React from 'react';

import {AgGridReact} from 'ag-grid-react';
import AgGridStatusBadgeRenderer from './AgGridStatusBadgeRenderer.jsx';

import ApiService from './../service/APIService';

import "../styles/KMRInitialTabContent.css";


class KMRInitialTabContent extends React.Component {

    apiService = new ApiService();

    constructor(props) {
        super(props);
        this.status = "initial";

        this.state = {
          columnDefs: [
                { headerName: "Claim Id", field: "id" },
                { headerName: "Member Name", field: "name" },
                { headerName: "Member SSN", field: "ssn" },
                { headerName: "Last Update", field: "date" },
                { headerName: "Status", field: "status", cellRenderer: 'gridStatusBadgeRenderer' }
            ],
            rowData: [
                
            ],
            frameworkComponents: {
                gridStatusBadgeRenderer: AgGridStatusBadgeRenderer,
            }
        }
    }

    componentDidMount() {
        this.fetchClaims();
    }

    fetchClaims() {
        var url = this.apiService.getApiUrl("getClaimsByStatus", ["initial"]);
        var root = this;
        this.apiService.invokeGet(url, this, function(response){
            var data = response.data;
            var temp = [];
            for(var i=0; i< data.length; i++) {
                var obj = {};
                obj.id = data[i].id;
                obj.name = data[i].memberName;
                obj.ssn = data[i].memberSSN;
                obj.date = "09/28/2020";
                obj.status = "Initial";

                temp.push(obj);
            }

            root.setState({'rowData': temp});
        })
    }

    onCellClicked(obj) {
        var cellVal = obj.data.id;
        window.location.href = "/portal/create/" + cellVal;

    }

    render() {
        return (
            <div id="grid-wrapper" style={{ width: '100%', height: '100%' }}>
                <div className="ag-theme-alpine ag-grid-container" style={ {height: '300px', width: '100%'} }>
                    <AgGridReact
                        columnDefs={this.state.columnDefs}
                        rowData={this.state.rowData}
                        frameworkComponents={this.state.frameworkComponents}
                        onGridSizeChanged={this.onGridSizeChanged.bind(this)}
                        onCellClicked={this.onCellClicked.bind(this)}
                    >
                    </AgGridReact>
                </div>
            </div>
        );
    }

    

    onGridSizeChanged = params => {
        var gridWidth = document.getElementById('grid-wrapper').offsetWidth;
        var columnsToShow = [];
        var columnsToHide = [];
        var totalColsWidth = 0;
        var allColumns = params.columnApi.getAllColumns();
        for (var i = 0; i < allColumns.length; i++) {
          var column = allColumns[i];
          totalColsWidth += column.getMinWidth();
          if (totalColsWidth > gridWidth) {
            columnsToHide.push(column.colId);
          } else {
            columnsToShow.push(column.colId);
          }
        }
        params.columnApi.setColumnsVisible(columnsToShow, true);
        params.columnApi.setColumnsVisible(columnsToHide, false);
        params.api.sizeColumnsToFit();
    };
}

export default KMRInitialTabContent;