import React from 'react';

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Alert from 'react-bootstrap/Alert';

import ApiService from './../../service/APIService';
import { Container, FormGroup } from 'react-bootstrap';

import "./style.css";
class MasterDataEntry extends React.Component {

    apiService = new ApiService();
    apiEndPointMap = {
        "coveragetypemaster" : "savecoveragetypemaster",
        "coveragetyperule" : "savecoveragetyperule",
        "networkmaster" : "savenetworkmaster",
        "networkrule" : "savenetworkrule",
        "claimtypemaster" : "saveclaimtypemaster",
        "claimtyperule" : "saveclaimtyperule",
        "exclusioncodemaster" : "saveexclusioncodemaster",
        "exclusioncoderule" : "saveexclusioncoderule",
        "statustypemaster" : "savestatustypemaster",
        "statustyperule" : "savestatustyperule",
        "supervisormaster" : "savesupervisormaster",
        "supervisorrule" : "savesupervisorrule",
        "eligibilityoverridereasonmaster" : "saveeligibilityoverridereasonmaster",
        "eligibilityoverridereasonrule" : "saveeligibilityoverridereasonrule",
        "reasonforholdingclaimmaster" : "savereasonforholdingclaimmaster",
        "reasonforholdingclaimrule" : "savereasonforholdingclaimrule",
        "adjudicationsequencemaster" : "saveadjudicationsequencemaster",
        "adjudicationsequencerule" : "saveadjudicationsequencerule",
        "providermaster" : "saveprovidermaster",
        "providerrule" : "saveproviderrule",
        "providerstatustypemaster" : "saveproviderstatustypemaster",
        "providerstatustyperule" : "saveproviderstatustyperule",
        "cptmaster" : "savecptmaster",
        "cptrule" : "savecptrule",
        "specialprovidercptmaster" : "savespecialprovidercptmaster",
        "specialprovidercptrule" : "savespecialprovidercptrule",
        "specialclaimtypecptmaster" : "savespecialclaimtypecptmaster",
        "specialclaimtypecptrule" : "savespecialclaimtypecptrule",
        "icd10master" : "saveicd10master",
        "icd10rule" : "saveicd10rule",
        "proceduremaster" : "saveproceduremaster",
        "procedurerule" : "saveprocedurerule",
        "procedurelimitsused" : "saveprocedurelimitsused",
        "limitsmaster" : "savelimitsmaster",
        "limitsused" : "savelimitsused"
    }

    constructor(props) {

        super(props);

        this.label = this.props.match.params.label;

        this.state = {

            item : "",
            mid: "",
            rowData: [],
            label: this.label,
            parentFields: [],
            childFields: [],
            childRows: [],
            formData: {},

            alertMessage: "",
            varient: "",
            showNotification: false
        }

    }

    componentDidMount() {
        this.fetchMasterDataItemFormFields();
    }

    createMasterItem() {

        //validation

        //1. Required fields
        for(var i=0; i< this.state.parentFields.length; i++) {
            if(this.state.parentFields[i].hidden != 'y' && this.state.parentFields[i].required == 'y') {

                if(this.state.formData[this.state.parentFields[i].identifier].trim().length == 0) {
                    this.setState({
                        alertMessage: this.state.parentFields[i].label + " is a required field",
                        varient: "danger",
                        showNotification: true
                    })
                    var root = this;
                    setTimeout(function() {
                        root.setState({
                            showNotification: false
                        })
                    }, 4000)
                    return;
                }
            }
        }


        //end validation
        
        var ep = this.apiEndPointMap[this.state.item.toLowerCase()];

        var url;

        if(this.state.mid == undefined) //which means master item entry
            url = this.apiService.getApiUrl("createMasterItem", [ep]);
        else //which means master child item entry
            url = this.apiService.getApiUrl("createMasterChildItem", [ep, this.state.mid]);

        console.log(this.state.formData);

        var root = this;
        this.apiService.invokePost(url, JSON.stringify(this.state.formData), this, function(response){
            if(response.status == 'success') {
                root.setState({
                    alertMessage: "Successfully added " + root.state.label,
                    varient: "success",
                    showNotification: true
                })

                root.resetForm();

                setTimeout(function() {
                    root.setState({
                        showNotification: false
                    })
                }, 4000)
            } else if(response.status == 'error') {
                var error = "";
                if(response.message.indexOf("duplicate key error collection") > 0)
                    error = "Same CPT number is already exists";

                root.setState({
                    alertMessage: "Error during saving " + root.state.label + " : " + error,
                    varient: "danger",
                    showNotification: true
                })

                setTimeout(function() {
                    root.setState({
                        showNotification: false
                    })
                }, 4000)
            }
        })
    }

    resetForm() {
        var temp = this.state.formData;
        for(var i=0; i< this.state.parentFields.length; i++) {
            if(this.state.parentFields[i].hidden != 'y') {
                temp[this.state.parentFields[i].identifier] = ""
            }
        }

        this.setState({
            'formData': temp
        })
    }

    fetchMasterDataItemFormFields() {

        var item = this.props.match.params.item;
        var mid = this.props.match.params.mid;

        if(item == undefined) return;
        this.setState({
            'item' : item,
            'mid' : mid
        })


        var url = this.apiService.getApiUrl("getmasterItemFormFields", [item]);
        var root = this;
        this.apiService.invokeGet(url, this, function(response){

            console.log(response);

            var crows = [];
            crows.push(response.data.child);

            root.setState({parentFields : response.data.parent});
            root.setState({childFields : response.data.child});
            root.setState({childRows : crows});
            var obj = {};

            for(var i=0; i< response.data.parent.length; i++) {
                obj[response.data.parent[i].identifier] = ""
            }

            //var childObj = {};
            //for(var i=0; i< response.data.child.length; i++) {
            //    childObj[response.data.child[i].identifier] = ""
            //}

            //obj[response.data.parentField] = childObj;
            root.setState({formData : obj})
        })
    }

    commonChangeHandler(event) {

        if(event.target.dataset.type == "number")event.target.value = event.target.value.replace(/[^0-9.]/g, ""); 

        var fdata = this.state.formData;
        fdata[event.target.dataset.name] = event.target.value
        this.setState({formData : fdata})
    }

    goToListItems() {

        if(this.state.mid == undefined) //means master data entry
            window.location.href = "/portal/masteritem/" + this.state.item + "/label/" + this.state.label;
        else //means child data entry 
        {
            var masterItem = this.state.item.replace("Rule", "") + "Master";
            var masterItemLabel = this.state.label.replace("Rules", "").trim();
            window.location.href = "/portal/masterdataedit/" + masterItem + "/label/" + encodeURI(masterItemLabel) + "/id/" + this.state.mid;
        }
    }

    render() {

        var root = this;
        return (
            <div className="list-container">
                <Row className="headerRow">
                    <Col>
                        <span className="h3-header"> Create { this.label } </span>
                    </Col>

                    <Col>
                        <Button className="claim-dashboard-btn mateffect" variant="primary" onClick={this.goToListItems.bind(this)}> 
                            { this.label } List
                        </Button>
                    </Col>
                </Row>

                <Container fluid>
                    <Alert key="alert" variant={this.state.varient} className={(this.state.showNotification ? 'show' : 'hidden')}>
                        {this.state.alertMessage}
                    </Alert>
                    <Form.Group as={Row}>
                    {
                        this.state.parentFields.map(function(field, index) {
                            if(field.type == "number")
                                return (
                                    <Col md={field.size} sm={field.size} className={(field.hidden == 'n' ? 'show' : 'hidden')}>
                                        <Form.Group as={Row} className="field-group">
                                            <Form.Label className={(field.required == 'y' ? 'required' : 'notrequired')}> 
                                                {field.label} 
                                            </Form.Label>
                                            <Form.Control 
                                                key={field.identifier}
                                                type="text"
                                                value={root.state.formData[field.identifier] || ""} 
                                                data-name={field.identifier}
                                                data-type={field.type}
                                                maxLength={field.datalength}
                                                onChange={root.commonChangeHandler.bind(root)}
                                            />
                                        </Form.Group>
                                    </Col>
                                )
                            else
                                return (
                                    <Col md={field.size} sm={field.size} className={(field.hidden == 'n' ? 'show' : 'hidden')}>
                                        <Form.Group as={Row} className="field-group">
                                            <Form.Label className={(field.required == 'y' ? 'required' : 'notrequired')}> 
                                                {field.label} 
                                            </Form.Label>
                                            <Form.Control 
                                                key={field.identifier}
                                                type={field.type}
                                                value={root.state.formData[field.identifier] || ""} 
                                                data-name={field.identifier}
                                                data-type={field.type} 
                                                maxLength={field.datalength}
                                                onChange={root.commonChangeHandler.bind(root)}
                                            />
                                        </Form.Group>
                                    </Col>
                                )
                        })
                    }
                    </Form.Group>

                    {/*<Form.Group as={Row} controlId="insuredId">
                        <Form.Label>
                            { this.state.label } Rules
                        </Form.Label>
                    </Form.Group>

                    {
                        this.state.childRows.map(function(row, index) {
                            return(
                                <Row>
                                    {
                                        row.map(function(field, elemIndex){
                                            return(
                                                <Form.Group as={Col}>
                                                    <Form.Label> {field.label} </Form.Label>
                                                    <Form.Control 
                                                        type={field.type}
                                                        value={root.state.formData[field.identifier]} 
                                                        data-name={field.identifier}
                                                    />
                                                </Form.Group>
                                            )
                                        })
                                    }
                                </Row>
                            )
                        })
                    }
                */}
                    
                    <Row>
                        <Col>
                            <Button className="claim-dashboard-btn mateffect" variant="primary" onClick={this.createMasterItem.bind(this)}> 
                                Submit
                            </Button>
                        </Col>
                    </Row>
                </Container>
            </div>
        )
    }

    onGridSizeChanged = params => {
        var gridWidth = document.getElementById('grid-wrapper-master-data-item').offsetWidth;
        var columnsToShow = [];
        var columnsToHide = [];
        var totalColsWidth = 0;
        var allColumns = params.columnApi.getAllColumns();
        for (var i = 0; i < allColumns.length; i++) {
          var column = allColumns[i];
          totalColsWidth += column.getMinWidth();
          if (totalColsWidth > gridWidth) {
            columnsToHide.push(column.colId);
          } else {
            columnsToShow.push(column.colId);
          }
        }
        params.columnApi.setColumnsVisible(columnsToShow, true);
        params.columnApi.setColumnsVisible(columnsToHide, false);
        params.api.sizeColumnsToFit();
    };
}

export default MasterDataEntry;