import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom'; 

import 'bootstrap/dist/css/bootstrap.min.css';

import KMRHeader from '../header/KMRHeader.jsx'
import KMRMenu from '../menu/KMRMenu.jsx';
import KMRContentArea from '../KMRContentArea.jsx';

import KMRClaims from '../KMRClaims';
import Login from '../login/Login.jsx';

import "./style.css";

class KMRParentComponent extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            loggedIn : "false"
        }
    }

    componentDidMount() {

        //check login
        var checkLogin = sessionStorage.getItem("loggedin");
        if(checkLogin == undefined || checkLogin == 0)this.setState({'loggedIn' : false})
        else this.setState({'loggedIn' : true})

    }

    render() {
        return (
            <>
                <div className={(!this.state.loggedIn ? 'show' : 'hidden')}>
                    <Login></Login>
                </div>
                <div className={(this.state.loggedIn ? 'show' : 'hidden')}>
                    <KMRHeader></KMRHeader>
                    <KMRMenu></KMRMenu>
                    <Router>
                        <Route exact path='/portal/:path1?/:path2?/:path3?/:path4?/:path5?/:path6?' component={KMRContentArea}></Route> 
                    </Router>
                </div>
            </>
        );
    }
}

export default KMRParentComponent;